import React, {Fragment} from 'react'
import {Link, withRouter} from "react-router-dom"
import { signout, isAuthenticated } from "../auth/helper"

const currentTab = (history, path) => {
    if (history.location.pathname === path) {
        return {color: "#2ecc72"}
    } else {
        return {color: "#FFFFFF"}
    }
}
const Menu = ({history}) => (
    <div>
        <ul className="nav nav-tags bg-dark">
            <li className="nav-item">
                <Link style={currentTab(history, "/")} className="nav-link" to="/">Home <i class="fa fa-home fa-lg" aria-hidden="true"></i></Link>
            </li>
            <li className="nav-item">
                <Link style={currentTab(history, "/cart")} className="nav-link" to="/cart">Cart <i class="fa fa-shopping-cart fa-lg" aria-hidden="true"></i></Link>
            </li>
            {isAuthenticated() && isAuthenticated().user.role === 0 && (
                <li className="nav-item">
                <Link style={currentTab(history, "/user/dashboard")} className="nav-link" to="/user/dashboard">Profile <i class="fa fa-user fa-lg" aria-hidden="true"></i></Link>
            </li>
            )}
            {isAuthenticated() && isAuthenticated().user.role === 1 && (
                <li className="nav-item">
                <Link style={currentTab(history, "/admin/dashboard")} className="nav-link" to="/admin/dashboard">Admin Profile <i class="fa fa-id-badge fa-lg" aria-hidden="true"></i></Link>
            </li>
            )}
            {!isAuthenticated() && (
                <Fragment>
                <li className="nav-item">
                    <Link style={currentTab(history, "/signup")} className="nav-link" to="/signup">Sign Up <i class="fa fa-user-plus fa-lg" aria-hidden="true"></i></Link>
                </li>
                <li className="nav-item">
                    <Link style={currentTab(history, "/signin")} className="nav-link" to="/signin">Sign In <i class="fa fa-sign-in fa-lg" aria-hidden="true"></i></Link>
                    </li>
                    </Fragment>
            )}
            {isAuthenticated() && (
                <li className="nav-item">
                    <span className="nav-link text-warning" onClick={() => {
                        signout(() => {
                            history.push("/");
                        });
                    }}>
                        Sign Out <i class="fa fa-sign-out fa-lg" aria-hidden="true"></i></span>
            </li>
            )}
        </ul>
    </div>
);

export default withRouter(Menu);