import React from "react"
import Base from "../core/Base"
import { isAuthenticated } from "../auth/helper/index"
import { Link } from "react-router-dom"

const UserDashBoard = () => {

    const {
        user: { name, email}
    } = isAuthenticated();

    const userprofile = () => {
        return (
            <div className="card mb-2 mx-6">
                <h4 className="card-header text-center">Profile <i class="fa fa-user fa-lg" aria-hidden="true"></i></h4>
                <ul className="list-group">
                    <li className="list-group-item">
                        <span className="badge badge-success mr-2">Name:</span>{name}
                    </li>
                    <li className="list-group-item">
                        <span className="badge badge-success mr-2">Email:</span>{email}
                    </li>
                </ul>
            </div>
        )
    };

    return (
        <Base title="User Profile" className="container bg-success p-3 mx-6">
            <div className="row">
                <div className="col-12 mx-0">{userprofile()}</div>
            </div>
        </Base>
    )
}

export default UserDashBoard;