import React, {useState, useEffect} from "react"
import Base from "../core/Base"
import { Link } from "react-router-dom"
import { isAuthenticated } from "../auth/helper"
import { getProducts, deleteProduct } from "./helper/adminapicall"

const ManageProducts = () => {

    const [products, setProducts] = useState([])

    const { user, token } = isAuthenticated();

    const preload = () => {
        getProducts().then(data => {
            if (data.error) {
                console.log(data.error);
            } else {
                setProducts(data);
            }
        });
    };

    useEffect(() => {
        preload();
    }, []);

    const deleteThisProduct = productId => {
        deleteProduct(productId, user._id, token).then(data => {
            if (data.error) {
                console.log(data.error);
            } else {
                preload();
            }
        })
    }

    return (
        <Base title="Welcome admin" description="Manage products here">
            <Link to="/admin/dashboard" className="btn btn-md btn-danger mb-3">
            <i class="fa fa-arrow-circle-left fa-lg" aria-hidden="true"></i></Link>
      <h2 className="mb-4 text-center">All Products</h2>
      <div className="row">
        <div className="col-12">
            {products.map((product, index) => {
                return (
              <div key={index} className="row text-center mb-2">
              <div className="col-4">
                <h4 className="text-white text-left">{product.name}</h4>
              </div>
              <div className="col-4">
                <Link
                  className="btn btn-success"
                  to={`/admin/product/update/${product._id}`}
                >
                  <span className="">Update <i class="fa fa-pencil-square-o fa-lg" aria-hidden="true"></i></span>
                </Link>
              </div>
              <div className="col-4">
                <button onClick={() => {deleteThisProduct(product._id)}} className="btn btn-warning">
                  Delete <i class="fa fa-trash-o fa-lg" aria-hidden="true"></i>
                </button>
                </div>
            </div>
                     )})}
        </div>
      </div>
    </Base>
    )
}

export default ManageProducts;