import React, {useState} from "react"
import Base from "../core/Base"
import { isAuthenticated } from "../auth/helper"
import { Link } from "react-router-dom"
import { createCategory } from "./helper/adminapicall"

const Addcategory = () => {

    const [name, setName] = useState("")
    const [error, setError] = useState(false)
    const [success, setSuccess] = useState(false)

    const { user, token } = isAuthenticated();

    const handleChange = (event) => {
        setError("");
        setName(event.target.value);
    };

    const onSubmit = (event) => {
        event.preventDefault();
        setError("");
        setSuccess(false);

        createCategory(user._id, token, { name })
            .then(data => {
                if (data.error) {
                    setError(true);
                } else {
                    setError("");
                    setName("");
                    setSuccess(true);
                }
            });
    };

    const setSuccessMessage = () => {
        if (success) {
            return <h5 className="text-success">Category created successfully</h5>
        }
    }

    const warningMessage = () => {
        if (error) {
            return <h5 className="text-warning">Failed to create category</h5>
        }
    }

    const myCategoryForm = () => {
        return(
            <form>
            <div className="form-group">
                <p className="lead text-white">Enter the category</p>
                <input type="text" className="form-control my-3" onChange={handleChange} autoFocus required placeholder="Ex. Classic,Comic,Action,...etc" />
                <button onClick={onSubmit} className="btn btn-outline-success">Create Category</button>
            </div>
        </form>
         ) };

    return (
        <Base title="Create category here" description="Add new category to your list"
            className="container bg-success p-4">
            <Link to="/admin/dashboard" className="btn btn-md btn-danger mb-3 my-0 mx-0"><i class="fa fa-arrow-circle-left fa-lg" aria-hidden="true"></i></Link>
            <div className="row bg-dark rounded">
                <div className="col-md-8 offset-md-1.5">
                    {setSuccessMessage()}
                    {warningMessage()}
                    {myCategoryForm()}
                </div>
            </div>
        </Base>
    )
}

export default Addcategory;